.title {
	margin-bottom: 15px;
	font-size: 27px;
	line-height: 1.3333333333;
}

@media (max-width: 767.98px) {
	.title {
		font-size: 20px;
	}
}

.title__accent {
	color: #0C8543;
}

.wrapper {
	min-height: 100vh;
	display: flex;
	flex-direction: column;
}

.main {
	flex-grow: 1;
}

.section {
	padding: 60px 0;
}

@media (max-width: 767.98px) {
	.section {
		padding: 35px 0;
	}
}

.bg-img {
	background-position: 50% 50%;
	background-repeat: no-repeat;
	background-size: cover;
}

.sr-only {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	padding: 0;
	border: 0;
	white-space: nowrap;
	clip-path: inset(100%);
	clip: rect(0 0 0 0);
	overflow: hidden;
}

img.lazyload {
	opacity: 0;
	transition: opacity 0.3s;
}

img.lazyload.lazyloaded {
	opacity: 1;
}

/* header */
.header {
	display: block;
	position: relative;
	padding: 20px 0;
	overflow: hidden;
}

@media (max-width: 767.98px) {
	.header {
		padding: 10px 0;
	}
}

.header::after {
	content: '';
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	border-bottom: 1px solid #E0E0E0;
	opacity: .98;
}

.header__logo {
	display: inline-flex;
	max-width: 168px;
	height: auto;
}

@media (max-width: 767.98px) {
	.header__logo {
		max-width: 150px;
	}
}

.header__logo img, .header__logo svg {
	width: 100%;
}

/* footer */
.footer {
	background-color: #0C8543;
	padding: 25px 0;
	color: #fff;
}

@media (max-width: 767.98px) {
	.footer {
		text-align: center;
	}
}

.footer__container {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

@media (max-width: 767.98px) {
	.footer__container {
		flex-direction: column;
	}
}

.footer__logo {
	display: inline-flex;
	width: 100%;
	max-width: 168px;
}

@media (max-width: 767.98px) {
	.footer__logo {
		max-width: 150px;
	}
}

@media (max-width: 767.98px) {
	.footer__nav {
		padding: 25px 0;
	}
}

.footer__list {
	padding: 0;
	font-size: 14px;
	line-height: 1.2142857143;
}

@media (min-width: 768px) {
	.footer__list {
		display: flex;
		flex-wrap: wrap;
	}
}

.footer__item {
	padding: 5px 15px;
}

@media (max-width: 1199.98px) {
	.footer__item {
		padding-left: 10px;
		padding-right: 10px;
	}
}

@media (max-width: 767.98px) {
	.footer__item {
		padding: 0;
	}
	.footer__item + .footer__item {
		margin-top: 15px;
	}
}

.footer__link {
	text-decoration: underline;
}

@media (hover), (min-width: 0\0), (min--moz-device-pixel-ratio: 0) {
	.footer__link:hover {
		text-decoration: none;
	}
}

.footer__note {
	flex-shrink: 0;
	max-width: 150px;
	font-size: 12px;
	line-height: 1.5;
}

@media (max-width: 767.98px) {
	.footer__note {
		max-width: none;
	}
	.footer__note br {
		display: none;
	}
}

/* hero */
.hero {
	padding: 50px 0 60px;
}

@media (max-width: 767.98px) {
	.hero {
		padding: 25px 0 35px;
	}
}

.hero__title {
	margin-bottom: 50px;
	font-size: 45px;
	font-weight: 600;
	line-height: 1.3333333333;
	text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
}

@media (max-width: 991.98px) {
	.hero__title {
		font-size: 35px;
	}
}

@media (max-width: 767.98px) {
	.hero__title {
		margin-bottom: 25px;
		font-size: 22px;
		line-height: 1.1818181818;
	}
}

.hero__accent {
	color: #0C8543;
	font-weight: 700;
}

@media (min-width: 992px) {
	.hero__accent {
		font-size: 50px;
		line-height: 1.2;
	}
}

.hero__row {
	--cardGutter: 20px;
	margin-left: calc(var(--cardGutter) * -0.5);
	margin-right: calc(var(--cardGutter) * -0.5);
	margin-bottom: calc(var(--cardGutter) * -1);
}

.hero__row > [class*="col"] {
	padding-left: calc(var(--cardGutter) * 0.5);
	padding-right: calc(var(--cardGutter) * 0.5);
}

@media (max-width: 767.98px) {
	.hero__row {
		--cardGutter: 8px;
	}
}

.hero__col {
	display: flex;
	margin-bottom: var(--cardGutter);
}

@media (max-width: 767.98px) {
	.hero__col:nth-child(1), .hero__col:nth-child(3) {
		order: -1;
	}
}

/* heroCard */
.heroCard {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 100%;
	padding: 30px 18px;
	background-color: #F3F9F6;
	text-align: center;
	font-size: 21px;
	line-height: 1.2857142857;
}

@media (max-width: 767.98px) {
	.heroCard {
		padding: 20px 16px;
		font-size: 16px;
	}
}

.heroCard__icon {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 70px;
	height: 70px;
	margin-bottom: 15px;
}

@media (max-width: 767.98px) {
	.heroCard__icon {
		width: 60px;
		height: 60px;
	}
}

.heroCard__icon img {
	max-height: 100%;
}

.heroCard__text {
	width: 100%;
}

@media (max-width: 767.98px) {
	.heroCard__text_change {
		font-size: 0;
	}
	.heroCard__text_change::after {
		content: attr(data-textСhange);
		font-size: 16px;
	}
}

/* faq */
.faq {
	background-color: #F3F9F6;
}

.faq__row {
	align-items: center;
}

.faq__row + .faq__row {
	margin-top: 40px;
}

@media (max-width: 767.98px) {
	.faq__row + .faq__row {
		margin-top: 30px;
	}
}

@media (min-width: 992px) {
	.faq__row:nth-child(2n) {
		flex-direction: row-reverse;
	}
}

.faq__text {
	margin-bottom: 0;
}

.faq__text:not(:last-child) {
	margin-bottom: 15px;
}

.faq__imgWrap {
	position: relative;
	margin: 0;
}

@media (max-width: 991.98px) {
	.faq__imgWrap {
		margin-top: 20px;
	}
}

.faq__img {
	display: block;
	width: 100%;
	border-radius: 8px;
}

.faq__img_additional {
	position: absolute;
	top: -20px;
	right: -20px;
	max-width: 240px;
	border-radius: 0;
}

@media (max-width: 991.98px) {
	.faq__img_additional {
		top: 10px;
		right: 10px;
	}
}

@media (max-width: 767.98px) {
	.faq__img_additional {
		max-width: 200px;
	}
}

/* about */
.about {
	text-align: center;
}

@media (max-width: 767.98px) {
	.about {
		display: none;
	}
}

.about__text {
	margin-bottom: 25px;
}

.about__row {
	margin-bottom: -30px;
}

.about__item {
	display: flex;
	margin-bottom: 30px;
}

/* aboutCard */
.aboutCard {
	width: 100%;
	background-color: #0C8543;
	color: #fff;
	font-size: 21px;
	line-height: 1.2857142857;
	border-radius: 8px;
}

.aboutCard__visual {
	position: relative;
	margin: 0;
	padding-bottom: 49.4117647059%;
	border-radius: 8px;
	overflow: hidden;
}

.aboutCard__img {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.aboutCard__body {
	padding: 15px;
}

/* warranty */
.warranty {
	background-color: #F6F6F6;
}

@media (max-width: 767.98px) {
